.background-items {
    width: 100%;
    min-width: 300px;
    max-width: 600px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    top: 50px;
    z-index: 1;
}
  
.phone {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding: 2px;
    width: 90px;
    height: 160px;
    min-width: 90px;
    min-height: 160px;
    background: white;
    border: 1px solid slategray;
    border-radius: 7px 7px 7px 7px;
    box-shadow: 1px 1px 2px 1px darkslategray;
    transform: rotate(340deg);
}

.phone-camera {
    width: 5px;
    height: 5px;
    background: black;
    border-radius: 50%;
    border: 1px solid slategray;
}

.phone-screen {
    width: 90%;
    height: 80%;
    background: black;
}

.phone-homebutton {
    width: 15px;
    height: 15px;
    color: white;
    border-radius: 50%;
    border: 2px inset slategray;
}

.passport {
    transform: rotate(20deg);
}

.passport-book {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    width: 110px;
    height: 150px;
    min-width: 110px;
    min-height: 150px;
    color: goldenrod;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    text-align: center;
    background: midnightblue;
    border-radius: 3px 3px 3px 3px;
    box-shadow: 1px 1px 2px 1px darkslategrey;
}

.passport-globe {
    font-size: 3em;
}